* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  margin-left: 30px;
  max-width: 320px;
  margin-bottom: 16px;
  font-size: 13px;
  border: 1px solid #cecece;
  background: #f6f6f6;
  border-radius: 8px;
}

.country-list {
  margin: 0;
}

.country__item {
  list-style: none;
  display: flex;
  align-items: center;
}

.text-b {
  display: inline-flex;
}

.div-text {
  margin-left: 30px;
}

.text {
  margin-left: 10px;
  font-weight: 800;
}

.span {
  font-weight: 300;
  &:not(:last-of-type):after {
    content: ',';
  }
}

.country-info__item {
  margin-left: 30px;
  display: flex;
  align-items: center;
}

.named__country {
  margin-left: 5px;
}
